.container {
  width: 100%;
  max-width: 1200px;
  padding: 30px;
  margin: 0 auto;
}

.iframeWrapper {
  position: relative;
  padding-top: 56.25%;
  margin-bottom: 20px;
  iframe {
    position: absolute;
    top: 0;
    left: 0;
    border: none;
    width: 100%;
    height: 100%;
  }
}

.content {
  .nameStats {
    display: flex;
    justify-content: space-between;
  }

  .modelName {
    font-size: 22px;
    font-weight: 500;
    line-height: 38px;
  }

  .description {
    margin-bottom: 10px;
    font-size: 14px;
    color: #555;
    word-break: break-word;
  }

  .statistic {
    min-width: 35px;
    display: flex;
    justify-content: flex-end;
    flex-shrink: 0;
  
    .stats {
      display: inline-block;
      font-style: normal;
      font-weight: 400;
      line-height: 3;
      margin-left: 8px;
      font-size: 16px;
      color: #ccc;
      white-space: nowrap;
      align-items: center;

      &:hover {
        color: #1caad9;
      }
    }
  
    .views::before {
      font-family: CustomIcons;
      content: '\e9ce';
      margin-right: 4px;
    }
  
    .likes::before {
      font-family: CustomIcons;
      content: '\e9d9';
      margin-right: 4px;
    }
  }

  .prop {
    font-size: 14px;
    color: #555;
    font-weight: 700;
  }

  .propName {
    font-weight: 400;
    margin-right: 5px;
  }

  .user {
    margin: 10px 0;
    display: flex;
    align-items: center;

    .avatar {
      width: 48px;
      height: 48px;
      margin-right: 10px;
      overflow: hidden;
      border-radius: 12%;

      img {
        width: 100%;
        height: 100%;
      }
    }

    .userName {
      font-size: 24px;
      font-weight: 700;
      color: #1caad9;
    }
  }

  .metaStats {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin: 12px 0;
    font-size: 16px;
    color: #555;

    .prop {
      margin-right: 25px;
      line-height: 1.5;
    }
  }

  .tags {
    display: flex;
    align-items: center;
    flex-wrap: wrap;

    .tag {
      display: flex;
      align-items: center;
      margin: 0 5px 5px 0;
      line-height: 30px;
      font-weight: 400;
      color: #777;
      background: #eee;
      border-radius: 4px;
      padding: 0 5px;
      font-size: 12px;
    }
  }
}