.cardWrap {
  margin: 0 11px 22px;
}

.card {
  display: block;
  text-decoration: none;
  width: 100%;
  background-color: #fff;
  position: relative;
  border-radius: 3px;
  box-shadow: 
    0 1px 1px rgb(97 108 121 / 10%),
    0 7px 18px 0 rgb(97 108 121 / 13%);
  transition: box-shadow .2s ease;

  &:hover {
    box-shadow: 
      0 1px 1px rgb(97 108 121 / 10%),
      0 7px 18px 0 rgb(21 111 170 / 21%);
  }
}

.main {
  position: relative;
  border-top-left-radius: 3px;
  border-top-right-radius: 3px;
  padding-top: 56.25%;

  img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.footer {
  display: flex;
  height: 48px;
  width: 100%;
  padding: 12px;
  align-items: center;
  justify-content: space-between;
}

.left,
.right {
  position: relative;
  display: flex;
  align-items: center;
}

.left {
  min-width: 0;
  margin-right: 6px;
  flex: 1;

  .avatar + .modelLink {
    max-width: calc(100% - 24px);
  }

  .avatar {
    display: inline-block;
    width: 18px;
    height: 18px;
    margin-right: 6px;
    overflow: hidden;
    vertical-align: middle;
    border-radius: 2px;

    & img {
      width: 100%;
      height: 100%;
    }
  }

  .modelLink {
    display: inline-flex;
    flex: 1;
    text-decoration: none;
    overflow: hidden;
    text-overflow: ellipsis;
    color: #555;

    &:hover {
      color: #1caad9;
    }

    .title {
      width: 100%;
      overflow: hidden;
      font-size: 12px;
      font-weight: 600;
      line-height: 17px;
      text-overflow: ellipsis;
      white-space: nowrap;
      vertical-align: middle;
    }
  }
}

.right {
  min-width: 35px;
  justify-content: flex-end;
  flex-shrink: 0;

  .stats {
    display: inline-block;
    font-style: normal;
    font-weight: 400;
    line-height: 1;
    margin-left: 8px;
    font-family: 'Open Sans', sans-serif;
    font-size: 12px;
    color: #ccc;
    white-space: nowrap;
    align-items: center;
  }

  .views::before {
    font-family: CustomIcons;
    content: '\e9ce';
    margin-right: 4px;
  }

  .comments::before {
    font-family: CustomIcons;
    content: '\e96d';
    margin-right: 4px;
  }

  .likes::before {
    font-family: CustomIcons;
    content: '\e9d9';
    margin-right: 4px;
  }
}

@media (min-width: 1100px) and (max-width: 1920px) {
  .cardWrap {
    width: calc(25% - 23px);
  }
}

@media (min-width: 800px) and (max-width: 1100px) {
  .cardWrap {
    width: calc(33.33333% - 23px);
  }
}

@media (min-width: 480px) and (max-width: 800px) {
  .cardWrap {
    width: calc(50% - 23px);
  }
}

@media (max-width: 480px) {
  .cardWrap {
    width: calc(100% - 23px);
  }
}
