.header {
  width: 100vw;
  background-color: transparent;

  .nav {
    margin: 0 auto;
    width: calc(100% - 60px);
    height: 40px;
  }

  .list {
    display: flex;
    height: 100%;
    align-items: center;
    list-style: none;

    .item {
      margin-right: 20px;
      display: inline-flex;
      justify-content: center;
      align-items: center;

      .link {
        display: block;
        color: #222;
        text-decoration: none;
        font-size: 12px;
        text-transform: uppercase;
        font-weight: 700;
        line-height: 3;

        &:hover {
          color: #1caad9;
        }
      }

      .active {
        text-decoration: underline;
        color: #1caad9;
      }
    }
  }
}