.message {
  position: absolute;
  z-index: 10;
  width: 100%;
  padding: 48px;
  text-align: center;

  h2 {
    margin: 10px 0;
    font-weight: 300;
    font-size: 48px;
  }

  a {
    margin: 0 5px;
    color: inherit;
    text-decoration: none;
    border-bottom: 1px solid #00000044;

    &:hover {
      border-bottom-color: #000;
    }
  }
}