.header {
  width: calc(100% - 40px);
  height: 60px;
  margin: 0 auto;
  display: flex;
  align-items: center;
  position: relative;

  &::after {
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    display: block;
    height: 1px;
    width: 100%;
    background: #e7e7e7;
    background: linear-gradient(90deg, rgba(231,231,231,0) 0%, rgba(231,231,231,1) 25%, rgba(231,231,231,1) 75%, rgba(231,231,231,0) 100%);
  }

  .setting {
    margin-right: 20px;
    position: relative;
    padding: 5px 0;
    font-size: 14px;
    color: #555;
    white-space: nowrap;
    display: flex;
    flex-flow: column nowrap;
    cursor: pointer;

    .fieldName {
      color: #999;
      font-size: 10px;
      text-transform: uppercase;
      line-height: 1.4;
    }

    select,
    input {
      border: none;
      color: #1caad9;
    }
  }
}

.searchForm {
  display: flex;
  flex: 1 1 auto;
  margin-left: 20px;

  .search {
    display: flex;
    flex: 1;
    height: 32px;
    padding-left: 5px;
    vertical-align: middle;
    background-color: #fff;
    border: 1px solid #ccc;
    border-radius: 4px;
    cursor: pointer;

    .searchIcon::before {
      content: '\e986';
      font-family: CustomIcons;
      display: block;
      padding: 2px 6px 2px 2px;
      font-size: 16px;
      line-height: 28px;
      color: #999;
    }

    &:hover .searchIcon::before {
      color: #1caad9;
    }

    input {
      display: block;
      width: 100%;
      height: 100%;
      font-size: 14px;
      font-family: 'Open Sans', sans-serif;
      letter-spacing: 1px;
      color: #000;
      background-color: transparent;
      border: none;
      outline: none;
    }
  }

  .searchBtn {
    position: relative;
    margin-left: 16px;
    min-height: 32px;
    max-width:  200px;
    font-size: 12px;
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: 1px;
    padding: 0 16px;
    color: #1caad9;
    background-color: transparent;
    border: 1px solid #1caad9;
    border-radius: 4px;
    transition: background-color 0.15s ease;
    cursor: pointer;

    &:hover {
      background-color: #e8f7fb;
    }
  }
}

.cards {
  margin: 20px auto;
  display: flex;
  flex-wrap: wrap;
  width: calc(100% - 20px);
}

.pagination {
  position: relative;
  margin: 20px;
  
  .btn {
    margin-left: 16px;
    min-height: 32px;
    max-width:  200px;
    font-size: 12px;
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: 1px;
    padding: 0 16px;
    color: #1caad9;
    background-color: transparent;
    border: 1px solid #1caad9;
    border-radius: 4px;
    transition: background-color 0.15s ease;
    cursor: pointer;

    &:hover {
      background-color: #e8f7fb;
    }

    &:disabled {
      visibility: hidden;
    }
  }
}

.overlay {
  position: fixed;
  top: 100px;
  left: 0;
  width: 100%;
  height: calc(100% - 60px);

  .empty {
    height: 70vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    .state {
      height: 40vh;
      max-height: 300px;
      width: 100%;
      background-repeat: no-repeat;
      background-position: center;
      background-size: contain;
      opacity: 0.1;
    }

    .noResults {
      background-image: url('../../assets/svgs/no-results.svg');
    }

    .trySearch {
      background-image: url('../../assets/svgs/empty.svg');
    }

    .loading {
      background-image: url('../../assets/loading-buffering.gif');
    }

    .text {
      margin-top: 20px;
      font-size: 22px;
      font-weight: 200;
      color: #999;
    }
  }

  &[area-hidden='true'] {
    display: none;
  }
}
